.VideoList {
    padding: 15px 0;
}

.VideoList-List {
    margin: 0;
    padding: 0;
    list-style: none;
}

.VideoList-Item {
    height: 30px;

    padding: 5px;

    -webkit-box-sizing: border-box;

            box-sizing: border-box;

    border-bottom: 1px dashed #aaa;

    cursor: pointer;

    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
}

.VideoList-Item_current {
    background: #ffdb4d;
}

.VideoList-RemoveItem {
    background: transparent;
    border: 0;
}



